.skills-body{
    height: 100vh;
    width: 100%;
    max-height: 100vh;
    background-color: #F0FFFF;
}

.skills-title{
    padding-top: 5em;
    padding-bottom: 0;
    text-align: center;
}

.skills-content{
   display: flex;
   height: 100%;
   align-items: center;
   justify-content: space-evenly;
    flex-wrap: wrap;
}

.skill-row-container{
    flex: 1;
}
.skills-imgBox{
    width: 100%;
    display: flex;
    justify-content: space-evenly;
}

.skills-pic{
    padding-top: 30px;
    padding-bottom: 30px;
    width: 25%;
    height: auto;
    @media screen and (max-width: 600px){
        width: 50%;
        height: auto;
    }
}