.contact-body{
    height: 100vh;
    width: 100%;
}


.contact-imgBox{
    width: 100%;
    text-align: center;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-content: center;
    height: 100%;
}

.contact-pic{
    width: 70px;
    max-width: 70px;
    height: auto;
    @media only screen and (max-width: 600px) {
        width: 50px;
    max-width: 50px;
    }
}