.experiences-body{
    height:100vh;
    @media screen and (max-width: 1200px){
        height:130vh;
    }
    @media screen and (max-width: 600px){
        height:250vh;
    }
    @media screen and (max-width: 500px){
        height:450vh;
    }
}


.experiences-card{
    width: 18rem;
    min-height: 35vh;
    margin-top: 20px;
    margin-bottom: 20px;
}

.experiences-card-body{
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    min-height: 100vh;
    height:auto;
    flex-wrap: wrap;
    overflow: hidden!important;
   
    @media screen and (max-width: 600px){
        max-height: 1000vh;
    }

}
